import {get, reUrl} from '../axiosconfig/axiosconfig'

export default {
    // IP地址使用率TOP5 及 总数统计
    get_ip_pool_summary: function(params) {
        return get(reUrl + 'ip_pool/get_ip_pool_summary/?page=1&page_size=-1', params)
    },
    // P子网使用率（超阈值）
    get_ip_net_summary: function(params) {
        return get(reUrl + 'ip_net/get_ip_net_summary/?page=1&page_size=-1', params)
    },
    // IP分配
    get_ip_summary: function(params) {
        return get(reUrl + 'ip/get_ip_summary/', params)
    },
    // IP资源池使用率 参数 start、end、ip_pool_id
    get_usage_rate_event: function(params) {
        return get(reUrl + 'ip_pool/get_usage_rate_event/', params)
    }
}
